
.take{
  cursor: pointer;
  transition: all 0.3s;

}

.take:hover{
  transform: scale(0.9);
  transition: all 0.3s;
}



.hidebutton{
  display: none;
}

.showbutton:hover .hidebutton{
  transition: all 9s ease-in-out;
  display: block;
}

/* ============================== */

.pageNumbers {
  list-style: none;
  display: flex;
}

.pageNumbers li {
  padding: 10px;
  border: 1px solid white;
  cursor: pointer;
}

.pageNumbers li.active {
  z-index: 3;
  color: #fff;
  background-color: #e00b05 !important;
  border-color: #353535;
}


.pageNumbers li button:focus {
  outline: none;
}




/* Pop-Up-Message */

.pop-up-message{
width: 68rem;
font-family: sans-serif;
position: relative;
}

/*   Media Pop Up Message  */

