@media (max-width: 767px) {
  .progress-circle {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .progress-circle .col-lg-3 {
    flex: 0 0 calc(50% - 0px);
    max-width: calc(50% - 0px);
    /* margin: 5px; */
  }

  .mx-1.shadow-lg.p-3.bg-body.rounded.showbutton {
    padding: 0.5rem !important;
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: hidden; /* Prevent any content from overflowing */
  }

  .take {
    width: 100%;
    height: 100px; /* Fixed height for image container */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; /* Prevent image from overflowing */
  }

  .lazyload {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
    object-fit: contain;
  }

  .fw-normal {
    font-size: 0.8rem;
    margin: 0.2rem 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .lead.fs-6 {
    font-size: 0.7rem;
    margin: 0.2rem 0;
  }

  .stars {
    font-size: 0.8rem;
  }

  .btn-outline-secondary {
    font-size: 0.7rem;
    padding: 0.3rem 0.5rem;
    margin-top: auto;
  }
}

/* For very small screens */
@media (max-width: 359px) {
  .progress-circle .col-lg-3 {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .take {
    height: 120px; /* Slightly larger image container for single-column layout */
  }
}



.shop-products-section {
min-height: 500px;
background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5)
  ),
  url('https://res.cloudinary.com/elonatech/image/upload/v1709811140/shopPage/shop_oby1yn.jpg');
background-repeat: no-repeat;
background-position: center;
background-size: cover;
display: flex;
justify-content: center;
align-items: center;
text-align: center;
color: white;
padding: 20px;
}

/* Text styling for desktop */
.shop-products-section h2 {
font-size: 2rem;
line-height: 1.2;
margin-bottom: 10px;
}

.shop-products-section h5 {
font-size: 1.3rem;
line-height: 1.2;
margin-bottom: 8px;
}

.shop-products-section p {
font-size: 1.1rem;
line-height: 1.2;
margin-bottom: 5px;
}

/* Mobile view */
@media (max-width: 576px) {
.shop-products-section {
      min-height: 200px; 
      background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url('https://res.cloudinary.com/elonatech/image/upload/v1730211773/shop_rksy9o.jpg');
      background-size: cover;
      flex-direction: column;
      align-items: center;
      padding-top: 50px;
}

/* Adjusting text for mobile view */
.shop-products-section h2 {
  font-size: 1.5rem;
  margin-bottom: 5px;
  line-height: 1.1;
}

.shop-products-section h5 {
  font-size: 1rem;
  margin-bottom: 3px;
  line-height: 1.1;
  font-weight: 200;
  /* display: none; */
}

.shop-products-section p {
  font-size: 0.9rem;
  line-height: 1.1;
  margin-bottom: 0;
  display: none;
}

.shop-products-section h2,
.shop-products-section h5,
.shop-products-section p {
  margin: 0;
  padding: 0;
}
}

.active-category {
background-color: #11253D;
color: white !important;
text-decoration: none !important;
padding: 5px 10px;
border-radius: 4px;
}



.add-to-cart {
  background-color: white;
  border: 1px solid #a9abae;
  transition: background-color 0.3s;
}

.add-to-cart:hover {
  background-color: #a9abae;
}


.showbutton {
  height: 400px; 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.take {
  flex-grow: 1; 
  display: flex;
  align-items: center;
  justify-content: center; 
}

.lazyload {
  max-width: 100%;
  max-height: 150px; 
}

.product-name {
  text-overflow: ellipsis; 
  overflow: hidden;
  white-space: nowrap; 
}

.stars {
  font-size: 1.2em;
}


.custom-container {
  --bs-gutter-x: 0;
  width: 100%;
  padding-right: 3px;
  padding-left: 3px;
}